___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --board-size: min(500px, 95vw);
  --cell-size: calc(var(--board-size) / 10);
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Courier New', Courier, monospace;
  margin: 0;
  padding: 1vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 4rem;
  text-align: center;
}

hr {
  width: 100%;
  margin: 1.5rem;
}

.board {
  display: grid;
  grid-template: repeat(10, 1fr) / repeat(10, 1fr);
  width: var(--board-size);
  aspect-ratio: 1;
  outline: 1px solid black;
}

.opponent.board > .cell:not([disabled]) {
  cursor: crosshair;
}

.opponent.board > .cell:not([disabled]):hover {
  background-color: #ddd;
}

.cell {
  font: inherit;
  border: none;
  background-color: #eee;
  outline: 1px solid black;
}

.cell.ship {
  background-color: #555;
}

.cell.hit {
  background-color: rgb(255, 133, 133);
}

.cell.miss {
  background-color: rgb(133, 255, 179);
}

.shipyard {
  display: flex;
  gap: 1rem var(--cell-size);
  flex-wrap: wrap;
  width: 500px;
  margin: 2rem auto;
}

.shipyard-ship {
  display: flex;
  cursor: grab;
}

.shipyard-ship.vertical {
  flex-direction: column;
}

.shipyard-ship > div {
  width: var(--cell-size);
  aspect-ratio: 1;
  background-color: #555;
  outline: 1px solid black;
}

.rotate,
.start {
  margin: 2rem auto;
  background-color: black;
  color: white;
  border: none;
  outline: none;
  padding: 1rem;
  border-radius: .5rem;
}

.rotate:hover,
.start:hover {
  outline: 2px solid black;
}

.rotate {
  display: block;
}

.start {
  display: none;
}

.win {
  font-size: 2rem;
  margin: 2rem auto;
  text-align: center;
}
